<template>
  <div class="card" style="height: 95%;">
    <div class="card__title">
      <b-row>
        <b-col style="max-width:max-content !important;">
          <span>New Prospect</span>
        </b-col>
        <b-col style="text-align: right;">
          <span>
            <ABSButton
              :text="'New'"
              classButton="button button--new"
              classIcon="icon-style-1"
              :disabled="ButtonStatus == null ? true : !ButtonStatus.btnAddNewProspect"
              @click="doAddNewProspect"
            />
          </span>

          <span>
            <ABSButton
              :text="'View All'"
              classButton="button button--new"
              classIcon="icon-style-1"
              :disabled="false"
              @click="doViewAllProspect"
            />
          </span>
        </b-col>
      </b-row>
    </div>
    <div class="card__body">
      <b-row>
        <b-col style="max-width: max-content !important;">
          <span @click="changeProspect('min')" style="cursor: pointer;">
            <font-awesome-icon class="isGrey" icon="less-than" size="lg" />
          </span>
        </b-col>
        <b-col style="text-align: center">
          <span>
            <h6 class="isGrey">{{ DataProspect.monthName + ' ' + DataProspect.year }}</h6>
          </span>
        </b-col>
        <b-col style="text-align: right;max-width: max-content !important;">
          <span @click="changeProspect('add')" style="cursor: pointer;">
            <font-awesome-icon class="isGrey" icon="greater-than" size="lg" />
          </span>
        </b-col>
      </b-row>
      <!-- <b-row class="row-dash-new-prospect-top" style="padding-top: 5%;"> -->
      <b-row style="position: absolute; width: 100%; bottom: 40%;">
        <b-col>
          <div class="buleth__blue" @click="doCustomer" style="cursor: pointer;">
            <span>{{ DataProspect.totalCustomer}}</span>
          </div>
          <div class="buleth-text" style="margin-top: 0px !important;">Total Customer</div>
        </b-col>
        <b-col>
          <div class="buleth__blue">
            <span>{{ DataProspect.target}}</span>
          </div>
          <div class="buleth-text" style="margin-top: 0px !important;">Target</div>
        </b-col>
        <b-col>
          <div class="buleth__green" @click="doAchievement_b" style="cursor: pointer;">
            <span>{{ DataProspect.achievement }}</span>
          </div>
          <div class="buleth-text" style="margin-top: 0px !important;">Achievement</div>
        </b-col>
      </b-row>

      <!-- <b-row class="row-dash-new-prospect-bottom" style="padding-top: 5%;"> -->
      <b-row style="position: absolute; width: 100%; bottom: 5px;">
        <b-col>
          <div class="prospect">
            <div class="prospect__title">Monthly Point</div>
            <div class="prospect__body">
              <b-row>
                <b-col md="4">
                  <div class="card">
                    <!-- <div class="container"> -->
                      <b-col style="text-align: center">
                        <b-col>
                          <span class="buleth-text isGrey" style="font-size:12px;">Target</span>
                        </b-col>
                        <b-col>
                          <h4 class="isGrey">
                            <b>{{ DataProspect.targetPoint}}</b>
                          </h4>
                        </b-col>
                      </b-col>
                    <!-- </div> -->
                  </div>
                </b-col>

                <b-col md="4" @click="doAchievement" style="cursor: pointer;">
                  <div class="card">
                    <!-- <div class="container"> -->
                      <b-col style="text-align: center">
                        <b-col>
                          <span class="buleth-text isGrey" style="font-size:12px;">Achievement</span>
                        </b-col>
                        <b-col>
                          <h4 class="isGrey">
                            <b>{{ DataProspect.achievementPoint}}</b>
                          </h4>
                        </b-col>
                      </b-col>
                    <!-- </div> -->
                  </div>
                </b-col>

                <b-col md="4" @click="doPending" style="cursor: pointer;">
                  <div class="card">
                    <!-- <div class="container"> -->
                      <b-col style="text-align: center">
                        <b-col>
                          <span class="buleth-text isGrey" style="font-size:12px;">Pending</span>
                        </b-col>
                        <b-col>
                          <h4 class="isGrey">
                            <b>{{DataProspect.pendingPoint}}</b>
                          </h4>
                        </b-col>
                      </b-col>
                    <!-- </div> -->
                  </div>
                </b-col>
              </b-row>

              <!-- <b-row>
                <b-col>
                  <span class="prospect__content">Target</span>
                </b-col>
                <b-col>
                  <span class="prospect__content">: {{ DataProspect.targetPoint}} Point</span>
                </b-col>
              </b-row>
              <b-row>
                <b-col>
                  <span class="prospect__content">Achievement Point</span>
                </b-col>
                <b-col>
                  <span class="prospect__content">: {{ DataProspect.achievementPoint}} Point</span>
                </b-col>
              </b-row>-->
            </div>
          </div>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      DataProspect: {
        month: "",
        monthName: "",
        year: "",
        totalCustomer: 0,
        target: 0,
        achievement: 0,
        targetPoint: 0,
        achievementPoint: 0,
        pendingPoint: 0
      }
    };
  },
  computed: {
    ButtonStatus() {
      return this.$store.getters.getButtonStatus;
    },
  },
  methods: {
    doCustomer() {
      this.doAchievement_b();
    },
    doAddNewProspect() {
      var url = "MK_CustomerForm";
      if (!url || url == "" || url == undefined) return;
      var param = {
        // option_url: this.getOptionUrl(),
        // title: this.title,
        isEdit: false,
        isView: false
      };
      this.$router.push({ name: url, params: param });
    },
    doViewAllProspect() {
      var url = "MK_DashboardProspect";
      if (!url || url == "" || url == undefined) return;
      var param = {
        // option_url: this.getOptionUrl(),
        // title: this.title,
        isEdit: false
      };
      this.$router.push({ name: url, params: param });
    },
    doAchievement() {
      this.$store.commit("setParamPage", {'where': 'C', marketing_id: null});
      this.$store.commit("setParamPage2", {'where': 'C', marketing_id: null});
      this.$router.push({ name: "MK_LogBook" });
    },
    doAchievement_b() {
      this.DataProspect.marketing_id = null;
      this.DataProspect.marketing_name = null;
      var param = {
        newProspect: this.DataProspect
      };
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "MK_CustomerDash" });
    },
    doPending() {
      this.$store.commit("setParamPage", {'where': 'P', marketing_id: null});
      this.$store.commit("setParamPage2", {'where': 'P', marketing_id: null});
      this.$router.push({ name: "MK_LogBook" });
    },
    getProspect(date = null) {
      var now = date ? new Date(date) : new Date();
      var month = now.getMonth();
      var year = now.getFullYear();

      this.DataProspect = {
        month: month + 1,
        monthName: this.getMonthName(now),
        year: year,
        target: 0,
        achievement: 0,
        targetPoint: 0,
        achievementPoint: 0,
        pendingPoint: 0
      };

      var param = {
        option_function_cd: "GetNewProspectDash",
        module_cd: "MK",
        ss_portfolio_id: this.getDataUser().portfolio_id,
        user_id: this.getDataUser().user_id,
        years: year,
        months: month + 1
      };
      this.CallFunction(param).then(ress => {
        if (ress == null) return;

        if (ress.Data.length > 0) {
          var data = ress.Data[0];

          this.DataProspect = {
            month: month + 1,
            monthName: this.getMonthName(now),
            year: year,
            target: data.target_prospect && data.target_prospect !== '' ? data.target_prospect : 0,
            achievement: data.achievement_prospect && data.achievement_prospect !== '' ? data.achievement_prospect : 0,
            targetPoint: data.monthly_point_target,
            achievementPoint: data.monthly_point_achievement
              ? data.monthly_point_achievement
              : 0,
            pendingPoint: data.monthly_point_pending,
            totalCustomer: data.total_customer && data.total_customer !== '' ? data.total_customer : 0,
          };
        } else {
          this.DataProspect = {
            month: month + 1,
            monthName: this.getMonthName(now),
            year: year,
            target: 0,
            achievement: 0,
            targetPoint: 0,
            achievementPoint: 0,
            pendingPoint: 0,
            totalCustomer: 0
          };
        }
      });
    },
    changeProspect(act) {
      var date = new Date();
      if (act === "min") {
        // var d = moment(date, 'YYYY-MM-DD').add(i, number)
        var mth =
          this.DataProspect.month && this.DataProspect.month !== ""
            ? parseInt(this.DataProspect.month)
            : 0;
        var year = this.DataProspect.year;
        mth = mth < 10 ? "0" + mth : mth;
        var frm = year + "-" + mth + "-" + "01";
        date = this.dateAdd2("m", frm, -1);
        // date = this.momentDateFormatting(date, frm)
      } else if (act === "add") {
        var mth =
          this.DataProspect.month && this.DataProspect.month !== ""
            ? parseInt(this.DataProspect.month)
            : 0;
        var year = this.DataProspect.year;
        mth = mth < 10 ? "0" + mth : mth;
        var frm = year + "-" + mth + "-" + "01";
        date = date = this.dateAdd2("m", frm, 1);
        // date = this.momentDateFormatting(date, frm)
      }

      this.getProspect(date);
    }
  },
  mounted() {
    this.getProspect();
  }
};
</script>